/* css for headers*/
.header {
  grid-column-start: 1;
  grid-column-end: 4;
}

header {
  font-family: 'Cardo', serif;
   letter-spacing:10px;
   opacity:.85;
  background-color: #666;
  padding: 10px;
  text-align: center;
  font-size: 1.5em;
  color: white;
  max-height: 60%;
}
/* css for main*/

.main {
  display: grid;
  grid-column-start: 1;
  grid-column-end: 4;
  grid-template-rows: auto;
  background-color: #307891;
  text-align: center; /*  min-width: 50%;
  min-height: 50%; */
  
  
}
.aside{
  width: 30%;
  float: left;
  height: 500px;
}

.aside p{
  width: 50%;
  padding: 5px;
  background: #307891; /* Fallback color for non-css3 browsers */
  height: 30px;
  margin-left: 30px ;
  /* Gradients */
  background: -webkit-gradient( linear,left top, left bottom, color-stop(0, #307891), color-stop(1, #307891));
  background: -moz-linear-gradient( center top, #307891 0%, #307891 100%);
  /* Rounded Corners */
  border-radius: 17px; 
  -webkit-border-radius: 17px;
  -moz-border-radius: 17px;
  /* Shadows */
  box-shadow: 1px 1px 2px rgba(0,0,0,.3), 0 0 2px rgba(0,0,0,.3); 
  -webkit-box-shadow: 1px 1px 2px rgba(0,0,0,.3), 0 0 2px rgba(0,0,0,.3);
  -moz-box-shadow: 1px 1px 2px rgba(0,0,0,.3), 0 0 2px rgba(0,0,0,.3);

}
.otherDays {
  float:left;
  padding: 20px;
  border-right: 8px double rgba(255,255,255,.8);
}

.text-center{
  text-align: center;
}
.today{
  margin: 0 auto;
     width: 70%;
     float: right; 
}

.today p,h3 {
  margin-right: 400px
  
}
h3{
  color: white;
}
p{
  font-size: 1.5em;
  color: #D1D5D7;
}
.temp{
  font-size: 3em;
  margin-bottom: 0px;
}
.search{
  padding: 5px;
  background: #e1e1e1; /* Fallback color for non-css3 browsers */
    width: 365px;
    margin: 30px auto;
    /* Gradients */
    background: -webkit-gradient( linear,left top, left bottom, color-stop(0, rgb(243,243,243)), color-stop(1, rgb(225,225,225)));
    background: -moz-linear-gradient( center top, rgb(243,243,243) 0%, rgb(225,225,225) 100%);
    /* Rounded Corners */
    border-radius: 20px; 
    -webkit-border-radius: 30px;
    /* Shadows */
    box-shadow: 1px 1px 2px rgba(0,0,0,.3), 0 0 2px rgba(0,0,0,.3); 
    -webkit-box-shadow: 1px 1px 2px rgba(0,0,0,.3), 0 0 2px rgba(0,0,0,.3);
    -moz-box-shadow: 1px 1px 2px rgba(0,0,0,.3), 0 0 2px rgba(0,0,0,.3);
}

.loader:empty {
  position: absolute;
  top: calc(50% - 4em);
  left: calc(50% - 4em);
  width: 6em;
  height: 6em;
  border: 1.1em solid rgba(0, 0, 0, 0.2);
  border-left: 1.1em solid #000000;
  border-radius: 50%;
  -webkit-animation: load8 1.1s infinite linear;
          animation: load8 1.1s infinite linear;
}

@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


